/* reset css */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1.1;
  font-family: "Noto Sans TC", "Roboto", sans-serif;
  font-size: 1rem;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Custom Additions Below */
:root,
* {
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
}

main, .header-container {
  padding: 1.6rem 1.8rem;
}

body {
  width: 100%;
  height: 100vh;
  /* header styling */
}
body header {
  max-width: 50rem;
}
body header nav.header-nav button#burger {
  transform: translateY(-0.5rem);
}
body header nav.header-nav img.logo-mobile {
  margin-left: 7rem;
}
body header nav.header-nav img.logo-desktop {
  display: none;
}
body header nav.header-nav ul.nav-list {
  position: absolute;
  left: 0.1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-top: 3rem;
  min-width: 100%;
  min-height: 100vh;
  background: white;
  font-size: 1.6rem;
  display: none;
}
body header nav.header-nav ul.nav-list.show {
  display: flex;
}
body header nav.header-nav ul.nav-list hr {
  border: 0;
  height: 0.1rem;
  width: 90%;
  background: rgba(41, 41, 41, 0.1);
}
body header nav.header-nav ul.nav-list .nav-icons {
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
}
body header nav.header-nav ul.nav-list .nav-icons img.shopping-cart-icon {
  margin: 0 4rem;
}

@media screen and (min-width: 1440px) {
  body {
    /* header styling */
  }
  body header {
    max-width: 144rem;
  }
  body header .header-container {
    padding: 0.8rem 16.7rem;
  }
  body header .header-container nav.header-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  body header .header-container nav.header-nav button#burger {
    display: none;
  }
  body header .header-container nav.header-nav img.logo-mobile {
    display: none;
  }
  body header .header-container nav.header-nav img.logo-desktop {
    position: absolute;
    display: block;
    transform: scale(0.6);
    left: 58rem;
    z-index: 1;
  }
  body header .header-container nav.header-nav ul.nav-list {
    top: -44.9rem;
    left: 56.5rem;
    display: flex;
    flex-direction: row;
    transform: translateX(-40rem);
    order: 1;
    gap: 3rem;
    min-width: unset;
    width: 100%;
  }
  body header .header-container nav.header-nav ul.nav-list hr {
    display: none;
  }
  body header .header-container nav.header-nav .nav-icons {
    order: 3;
    margin-left: 57rem;
  }
  body header .header-container nav.header-nav .nav-icons a img.search-icon {
    margin-top: 0.2rem;
  }
  body header .header-container nav.header-nav .nav-icons a img.shopping-cart-icon {
    margin-left: 3rem;
    margin-right: 3rem;
  }
}